.upload-localimg .btn {
  margin-left: -1px;
  border: none;
  line-height: 20px;
  margin-right: 5px;
  padding: 0;
  color: #555;
  min-width: 30px;
  height: 30px;
  width: 30px;
}

.upload-localimg-disabled .btn {
  cursor: default;
}

.upload-localimg-hover .btn:hover {
  background-color: #e5e5e5;
}

.upload-localimg-disable .btn {
  color: #999999;
}

.seafile-ed-dropbtn:hover {
  color: #16181b;
  text-decoration: none;
  background-color: #f8f9fa;
}
.seafile-ed-dropbtn .seafile-ed-localimgspan {
  position: relative;
  display: block;
  overflow: hidden;
}
.seafile-ed-dropbtn .seafile-ed-localimgspan .seafile-ed-localimginput {
  position: absolute;
  right: 0;
  top: 0;
  filter: alpha(opacity=0);
  opacity: 0;
}
.upload-local-image-word {
  color: #6e7687;
  display: block;
}

.insert-file {
  position: relative;
  display: inline-block;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}
.insert-file input {
  position: absolute;
  right: -7px;
  top: -7px;
  opacity: 0;
  filter: alpha(opacity=0);
  width: 40px;
  height: 40px;
}
.insert-file:hover {
  background-color: #e5e5e5;
}
.insert-file-disabled:hover {
  background-color: #fff;
}
.insert-file-disabled input {
  color: #999;
}
.button-container .toggle-side-panle-icon {
  position: absolute;
}
.rich-menu .side-panel-btn {
  display: none;
}
@media (max-width: 991.8px) {
  .button-container .toggle-side-panle-icon {
    display: none;
  }
  .rich-menu .side-panel-btn {
    display: block;
  }
}